import React, { Fragment } from 'react';
import { Dialog as HeadlessDialog, Transition } from '@headlessui/react';

export interface DialogProps extends React.HTMLAttributes<HTMLDivElement> {
  readonly isOpen: boolean;
  readonly onClose: (value: boolean) => void;
  readonly showOverlay?: boolean;
  readonly initialFocus?: React.MutableRefObject<HTMLElement | null> | undefined;
}
export const Dialog: React.FC<DialogProps> = ({
  isOpen,
  onClose,
  showOverlay,
  children,
  initialFocus,
  ...props
}) => {
  return (
    <Transition appear show={isOpen} as={Fragment}>
      <HeadlessDialog
        onClose={onClose}
        {...props}
        as="div"
        className="fixed inset-0 z-10 overflow-y-auto"
        initialFocus={initialFocus}
      >
        <Transition.Child
          as="div"
          enter="ease-out duration-100"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-100"
          leaveFrom="opacity-100"
          leaveTo="opacity-0"
        >
          {showOverlay && <HeadlessDialog.Overlay className="fixed inset-0 bg-black opacity-30" />}
          <div className="flex items-center justify-center min-h-screen">{children}</div>
        </Transition.Child>
      </HeadlessDialog>
    </Transition>
  );
};
